:root {
    --primary-color: #2c3e50;
    --secondary-color: #3498db;
    --background-color: #f4f6f7;
    --text-color: #34495e;
    --border-radius: 8px;
    --box-shadow: 0 4px 6px rbga(0,0,0,0.1);
}

.fitness-app-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.video-progress-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 640px;
    height: 360px;
}

.fitness-app-title {
    text-align: center;
    color: var(--primary-color);
    margin-bottom: 2rem;
}

.exercise-form {
    display: flex;
    flex-direction: column;
}

.form-label {
    margin-bottom: 0.5rem;
    color: var(--text-color);
}

.exercise-dropdown,
.weight-input,
.file-upload {
    width: 100%;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: var(--border-radius);
    transition: border-color 0.3s ease;
}

.exercise-dropdown:focus,
.weight-input:focus {
    outline: none;
    border-color: var(--background-color);
}

.submit-button {
    background-color: var(--secondary-color);
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: var(--border-radius);
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-top: 1rem;
}

.submit-button:hover {
    background-color: #2980b9;
}

.analyzing-message {
    text-align: center;
    color: var(--secondary-color);
    margin: 1rem 0;
}

.video-container, .progress-image {
    display: flex;
    width: 50%;
    height: 100%;
    object-fit: contain;
}

.chatbot-response {
    margin-top: 1rem;
    padding: 1rem;
    background-color: var(--background-color);
    border-radius: var(--border-radius);
    text-align: center;
}

.signoff-button {
    display: block;
    width: 200px;
    margin: 2rem auto;
    background-color: var(--background-color);
    color: #2c3e50;
    text-align: center;
    padding: 10px 20px;
    border-radius: var(--border-radius);
    text-decoration: none;
    cursor: pointer;
    transition: background-color 0.3s ease;    
}

.progress-button:hover {
    background-color: #34495e;
}

@media (max-width: 600px) {
    .fitness-app-container{
        padding: 1rem;
        margin: 1rem;
    }
}